<template>
    <v-row class="pa-12">
        
        <v-data-table hide-default-footer :items="value" :headers="headers" v-if="value.length" style="width: 100%" >
            <template v-slot:item.createdAt="{item}" >
                {{dateformat(item.createdAt, 'yyyy-mm-dd hh:MM:ss')}}
            </template>
        </v-data-table>
         
        <v-alert v-else type="warning" width="100%">No data available  </v-alert>
    </v-row>
</template>
<script>
import dateformat from 'dateformat'
 
export default {
    name: 'Assignment',
    props: {
        value: Array,
        default: () => []
    },
    data () {
        return {
            dateformat,
            headers: [
                { text: 'Assigned To', value: 'assignedTo.displayName' },
                { text: 'Assigned By', value: 'assignedBy.displayName' },
                { text: 'Date', value: 'createdAt' },
            ]
        }
    } 

}
</script>