<template>
  <v-container >
 
        <h1 class="headline my-4">
            <v-icon size="30">{{ collection.icon }}</v-icon>
             {{ title }} 
        </h1>
        
        <v-toolbar flat fixed :dense="$vuetify.breakpoint.xsOnly" v-if="collectionButtons">
          <template  v-for="(button, index) in collectionButtons"  >
            <v-spacer :key="index" v-if="button.spacer"></v-spacer> 
            <v-btn 
                    v-else
                    :key="index" 
                    :small="$vuetify.breakpoint.xsOnly"
                    class="mx-1"
                
                    v-bind="typeof button.bind == 'function' ? button.bind(vm) : button.bind"
                    v-on="typeof button.on == 'function' ? button.on(vm) : button.on"
                    outlined>
                    <v-icon v-if="button.icon">{{button.icon}}</v-icon>
                    <span v-if="!$vuetify.breakpoint.xsOnly"> {{button.title}} </span>
                </v-btn>
       
          </template>

        </v-toolbar>
     
        <v-form ref="form">
          <v-row>
            <v-col cols="12" md="8"  id="content">
              <v-card v-for="(panel, index) in contentPanels" :key="index" class="mb-4">
                <v-card-title v-if="panel.title">
                  <v-skeleton-loader v-if="loading" type="card-heading" width="100%"  ></v-skeleton-loader>
                  <span v-else>
                    <v-icon v-if="panel.icon" class="mx-1" size="25">
                      {{ panel.icon }}
                    </v-icon>
                    {{panel.title}}
                  </span>
                </v-card-title>
                <v-card-text class="py-0">
                  <v-row>
                    <v-col cols="12" v-for="(field, ind) in filterFields(panel.fields)" :key="ind" v-bind="field.col">
                       
                      <component 
                          :is="inputs[field.input]"
                          v-if="!loading"
                          outlined
                          dense 
                          v-bind="getProps(typeof field.bind == 'function' ? field.bind(vm) : field.bind, `${collectionName}_${field.name}`)"
                          v-on="typeof field.on == 'function' ? field.on(vm) : field.on"
                          v-model="data[field.name]"
                        />
                        <v-skeleton-loader v-else type="list-item"  class="pa-0"></v-skeleton-loader>

                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>  
            </v-col>
            <v-col cols="12" md="4"   id="sidebar">
              <v-card v-for="(panel, index) in sidebarPanels" :key="index" class="mb-4" >
                <v-card-title v-if="panel.title">
                  <v-skeleton-loader v-if="loading" type="card-heading" width="100%"  ></v-skeleton-loader>
                  <span v-else>
                    <v-icon v-if="panel.icon" class="mx-1" size="25">
                      {{ panel.icon }}
                    </v-icon>
                    {{panel.title}}
                  </span>
                </v-card-title>
                <v-card-text class="py-0">
                  <v-row>
                    <v-col cols="12" v-for="(field, ind) in filterFields(panel.fields)" :key="ind" v-bind="field.col">
                      <component 
                          :is="inputs[field.input]"
                          v-if="!loading"
                          outlined
                          dense 
                          v-bind="getProps(typeof field.bind == 'function' ? field.bind(vm) : field.bind, `${collectionName}_${field.name}`)"
                          v-on="typeof field.on == 'function' ? field.on(vm) : field.on"
                          v-model="data[field.name]"
                        />
                        <v-skeleton-loader v-else type="list-item"  class="pa-0"></v-skeleton-loader>
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>  
            </v-col>
          </v-row>
        </v-form>
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  <br />
  </v-container>
</template>

<script>

import collections from './../../collections'

 import   {
  VTextField,
  VSelect,
  VSwitch,
  VTextarea
} from 'vuetify/lib'
 
import AppAutoComplete from './../../components/Autocomplete'
import AppDatePicker from './../../components/DatePicker'
import AppDateRange from '../../components/DateRange'
import Assignment from '../../components/Assignment'
import ProfileImage from '../../components/ProfileImage'
import AppPassword from '../../components/Password'
import AppMenuItemInput from '../../components/MenuItemInput'
import Rights from '../../components/Rights'
 
export default {
  name: 'Item',
  data: () => ({
    data:{},
    props: {},
    loading: false
  }),
  computed: {
    vm () {
      return this
    },
    inputs: () => ({
            text: VTextField,
            textarea: VTextarea,
            select: VSelect,
            switch: VSwitch,
            date: AppDatePicker,
            range: AppDateRange,
            autocomplete: AppAutoComplete,
            assignment: Assignment,
            'profile-image':ProfileImage,
            password: AppPassword,
            rights: Rights,
            'menu-items': AppMenuItemInput
    }),
    id () {
      return this.$route.params.id
    },
    clone () {
      return this.$route.query.clone
    },
    isNew () {
      return this.id.toLowerCase() == 'new'
    },
    isClone () {
      return  Boolean(this.clone)
    },
    title () {
      return this.isNew ? `New ${this.collectionTitle}` : this.collectionTitle
    },
    collectionName () {
        return this.$route.params.collection
    },
    collection () {
        return collections.get(this.collectionName)
    },
    collectionButtons () {
      if (!this.collection.item.buttons) return false
      return this.collection.item.buttons.filter(btn => {
         if (btn.if){
          switch (typeof btn.if) {
            case 'function':
              return btn.if(this);
            case 'boolean':
              return btn.if;
            default:
              return true;
          }
        } else
          return true
      })
    },
    collectionTitle () {
        return this.collection.title.singular || this.collection.title
    },
    sidebar () {
      return this.collection.item.sidebar
    },
    sidebarPanels () {
      return this.filterPanel(this.sidebar.panels)
    },
    content () {
      return this.collection.item.content
    },
    contentPanels () {
       return this.filterPanel(this.content.panels)
    },
    fields () {
      // get all fields from panels
      const panels = [...this.content.panels, ...this.sidebar.panels]
      let fields = []
      panels.forEach(panel => {
        if (panel.fields)
          fields = fields.concat(panel.fields)
      })
      return fields
    }
    
  },
  watch:{
    '$route': {
      handler: 'updateComponent',
      immediate: true
    }
  },
  methods: {
    filterPanel (panels) {
      return panels.map(panel => {
        const copy = Object.assign({}, panel);
        copy.fields = this.filterFields(panel.fields)
        return copy
      }).filter(panel => panel.fields.length)
    },
    filterFields (fields) { 
      const copy = Object.assign([], fields);
      return copy.filter(field => {
        if (field.if){
          switch (typeof field.if) {
            case 'function':
              return field.if(this);
            case 'boolean':
              return field.if;
            default:
              return true;
          }
        } else
          return true
      })
    },
    getProps (props, refName) {
      if (this.props[refName]) {
         return Object.assign({}, props, this.props[refName])
      } else {
         return props
      }
    },
    resetComponent () {
      this.data = {}
      this.props = {}
    },
    updateComponent () {
      this.resetComponent()
      this.setDataValues()
      this.fetch()
    },

    async fetch () {
      if (!this.isNew || this.isClone) {
        this.loading = true
        const id = this.isClone ? this.clone : this.id
        const clonedFields =  this.$route.query.fields
        const result = await this.getItem({ collection: this.collectionName, id }) 
        
        
        // not found 
        if (!result || !result.data)
          this.$router.replace('/404')
   
        
        
        this.fields.forEach(field => {
            if (field.name && result.data[field.name]) {
              //ignore password value
              if (!this.isNew && field.input == 'password') return;


              // clone a choosen fields
              if (this.clone && clonedFields && clonedFields.length) {
                if (clonedFields.indexOf(field.name) !== -1)
                  //set field value
                  this.data[field.name] = result.data[field.name];
              } else
                //set field value
                this.data[field.name] = result.data[field.name]
            }
        })
       
        
        
        this.loading = false

      }
    },
    setDataValues () {
      const data = {}
      
      // loop all fields
      this.fields.forEach(field => {
        if (!field.name) return
        //set default value to null
        data[field.name] = null

        //set default value from field
        if (Object.prototype.hasOwnProperty.call(field,'default')) 
          if (typeof field.default == 'function')
            data[field.name] = field.default(this)
          else
            data[field.name] = field.default

      }) // end loop


      this.data = data

    } 
  }
}
</script>

<style>

</style>