<template>
   <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        
        transition="scale-transition"
        min-width="290px"
      >
    <template v-slot:activator="{ on }"> 
      
      <v-text-field   :value="dateRangeText"   readonly hide-details v-bind="$attrs" v-on="on" clearable @click:clear="clear"></v-text-field>
    </template>
   
   <v-date-picker v-model="dates" range @input="input"></v-date-picker>
   </v-menu>
</template>
<script>
 
 
export default {
  name: 'date-picker',
  props: {
    value: Array,
  },
  data() {
    
    return {
      menu: false,
      dates:[]
    }
  },
  methods: {
    input(e ) {
      if (e.length > 1) {
         this.menu = false
         this.$emit('input', e)
      }
    },
    clear () {
      this.dates = []
      this.$emit('input', [])
    }

  },
  computed: {
    dateRangeText () {
      return this.dates.join(' ~ ')
    },
     
  },
  created () {
    if (this.value)
      this.dates = this.value
    // else
    //    this.$emit('input', this.dates)
  }
 
}
</script>